.navbar {
  position: fixed;
  top: 10px;
  left: 10px;
  background-color: rgba(255, 255, 255, 0.9);
  padding: 10px;
  border-radius: 5px;
  z-index: 1100;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

.navbar select {
  font-size: 16px;
  padding: 5px;
}

.overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.8);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000;
  transition: opacity 0.5s;
  opacity: 0;
  pointer-events: none;
}

.overlay.visible {
  opacity: 1;
  pointer-events: auto;
}

.full-screen-image {
  max-width: 90%;
  max-height: 90%;
}

.blurred {
  filter: blur(5px);
  transition: filter 0.5s;
}

